
.fade-enter-active, .fade-leave-active {
        -webkit-transition: opacity .5s;
        transition: opacity .5s
}
.fade-enter, .fade-leave-active {
        opacity: 0
}
.item{
      height: 100px;
      -webkit-box-flex:1;
          -ms-flex-positive:1;
              flex-grow:1;   
      margin: 2px;
}
.item img{
      height: 100px;
      min-width: 100%;     
      max-width: 100%;
/*       background-color: red;
 */      -o-object-fit: cover;      object-fit: cover;     
      vertical-align: bottom;
}
.photo{
        position: fixed;
        width:100%;
        top:64px;
    	height:547px;
        left:0;
		text-align: center;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
        overflow-y : auto;
}
.photo-content{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}



