
.admin_icon{
    width:120px;
    height: 120px;
    border-radius: 50%;
    overflow: hidden;
    margin:20px auto;
}
.login-user{
        width:100%;
        height: 50px;
        background-color:#fff;
        position: fixed;
        top:0;
        left:0;
        line-height: 50px;
        opacity: .8;
        -webkit-animation: loginUser 5s ease forwards;
                animation: loginUser 5s ease forwards;
        z-index: 2;
}
@-webkit-keyframes loginUser {
0%   {top:-50px;
}
30%  {top : 0;
}
70%  {top : 0;
}
100% {top:-50px
}
}
@keyframes loginUser {
0%   {top:-50px;
}
30%  {top : 0;
}
70%  {top : 0;
}
100% {top:-50px
}
}
.login{
        width:100%;
        height:100%;
        background-size: cover;
        background:url(../../../static/img/bg.jpeg);
        overflow: hidden;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
}
.login form {
        width:70%;
}
.login-icon{
        min-width: 120px;
        min-height: 120px;
        width: 120px;
        height: 120px;
        border-radius: 50%;
}
.loading{
        position: fixed;
        top:50%;
        left:50%;
        margin-left:-40px;
        margin-top:-40px;
}
.loading_bg{
        background-color:#fff;
        opacity: .4;
        width:100%;
        height:100%;
        position: fixed;
        top:0;
        left:0;
}



